import { createRouter, createWebHashHistory } from 'vue-router'


const routes = [
    { path: '/', component: () => import('../views/Home.vue') },
    { path: '/el-trabajo-interior/', component: () => import('../views/ElTrabajoInterior.vue'),
        children: [
            { path: '', component: () => import('../views/ElTrabajoInteriorIntro.vue') },
            { path: 'que-es-el-trabajo-interior', component: () => import('../views/QueEsElTrabajoInterior.vue') }
        ]
     },
    { path: '/el-potencial', component: () => import('../views/ElPotencial.vue'),
        children: [
            { path: '', component: () => import('../views/ElPotencialIntro.vue') },
            { path: 'que-es-el-potencial', component: () => import('../views/QueEsElPotencial.vue') },
            { path: 'el-hombre-como-potencial', component: () => import('../views/ElHombreComoPotencial.vue') },
            // { path: 'ser-lo-que-ya-somos', component: () => import('../views/SerLoQueYaSomos.vue') },
            // { path: 'el-bloqueo-del-potencial', component: () => import('../views/ElBloqueoDelPotencial.vue') }
        ]
    }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
