<template>
    <div class="visibilidad" @click="mostrar">
        <div v-if="contador > 2">
              <header>
                <h1 class="titulo">
                    <router-link to="/"> Inicio </router-link>
                </h1>
                <div class="boton-menu" @click="toggleMenu"> 
                    &#8801; 
                </div>
            </header>
            <div class="container-app">
                <div class="menu">
                    <MenuLateral></MenuLateral>
                </div>
                <div class="vistas" :class="[ $store.state.menuAbierto ? 'derecha' : 'izquierda' ]">
                    <div class="capa-para-centrado">
                        <router-view/>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>


<script>
import store from '@/store'
import MenuLateral from '@/components/MenuLateral.vue'

export default {
    name: 'App',
    data() {
        return {
            contador: 0,
            abierto: true
        }
    },
    methods: {
        mostrar() {
            this.contador ++
        },
        toggleMenu() {
            if( window.innerWidth - 700 ) {
                store.commit('abrirCerrarMenu')
            }
        }
    },
    components:{
        MenuLateral
    }
}
</script>


