import { createStore } from 'vuex'

export default createStore({
  state: {
      menuAbierto: true
  },
  mutations: {
      abrirCerrarMenu (state) {
          state.menuAbierto = !state.menuAbierto
      },
      abrirMenu (state) {
        state.menuAbierto = true
      },
      cerrarMenu (state) {
        state.menuAbierto = false
      }
  },
  actions: {
  },
  modules: {
  }
})
